import LayoutStrategyBase2 from "./LayoutStrategyBase";
import Dimensions2 from "./Dimensions";
import LayoutAlgorithm2 from "./LayoutAlgorithm";
import BranchParentAlignment2 from "./BranchParentAlignment";
import Size2 from "./Size";
import Rect2 from "./Rect";
import StackOrientation2 from "./StackOrientation";
export default class StackingLayoutStrategy extends LayoutStrategyBase2 {
  constructor() {
    super();
    this.GetSupportsAssistants = () => false;
    this.Orientation = StackOrientation2.SingleRowHorizontal;
    this.ParentAlignment = BranchParentAlignment2.InvalidValue;
    this.ChildConnectorHookLength = 0;
    this.ParentConnectorShield = 0;
    this.SiblingSpacing = 5;
  }
  PreProcessThisNode(state, node) {
    node.State.NumberOfSiblings = node.Element.IsCollapsed ? 0 : node.ChildCount;
    if (node.State.NumberOfSiblings > 0) {
      if (this.Orientation != StackOrientation2.SingleRowHorizontal && this.Orientation != StackOrientation2.SingleColumnVertical) {
        throw new Error("InvalidOperationException: Unsupported value for orientation: " + this.Orientation);
      }
    }
  }
  ApplyVerticalLayout(state, level) {
    let node = level.BranchRoot;
    if (node.Level == 0) {
      node.State.SiblingsRowV = new Dimensions2(node.State.Top, node.State.Bottom);
    }
    if (node.State.NumberOfSiblings == 0) {
      return;
    }
    let siblingsRowExterior = Dimensions2.MinMax();
    if (this.Orientation == StackOrientation2.SingleRowHorizontal) {
      let top = node.AssistantsRoot == null ? node.State.SiblingsRowV.To + this.ParentChildSpacing : node.State.BranchExterior.Bottom + this.ParentChildSpacing;
      for (let i = 0; i < node.State.NumberOfSiblings; i++) {
        let child = node.Children[i];
        let rect = child.State;
        LayoutAlgorithm2.MoveTo(child.State, 0, top);
        child.State.BranchExterior = Rect2.from(child.State.Size, child.State.TopLeft);
        siblingsRowExterior = Dimensions2.add(siblingsRowExterior, new Dimensions2(top, top + rect.Size.Height));
      }
      siblingsRowExterior = new Dimensions2(siblingsRowExterior.From, siblingsRowExterior.To);
      for (let i = 0; i < node.State.NumberOfSiblings; i++) {
        let child = node.Children[i];
        child.State.SiblingsRowV = siblingsRowExterior;
        LayoutAlgorithm2.VerticalLayout(state, child);
      }
    } else if (this.Orientation == StackOrientation2.SingleColumnVertical) {
      let prevRowExterior = new Dimensions2(node.State.SiblingsRowV.From, node.State.SiblingsRowV.To);
      for (let row = 0; row < node.State.NumberOfSiblings; row++) {
        let child = node.Children[row];
        let rect = child.State;
        let top = prevRowExterior.To + (row == 0 ? this.ParentChildSpacing : this.SiblingSpacing);
        LayoutAlgorithm2.MoveTo(child.State, rect.Left, top);
        child.State.BranchExterior = Rect2.from(child.State.Size, child.State.TopLeft);
        let rowExterior = new Dimensions2(top, top + rect.Size.Height);
        child = node.Children[row];
        child.State.SiblingsRowV = rowExterior;
        LayoutAlgorithm2.VerticalLayout(state, child);
        let childBranchBottom = child.State.BranchExterior.Bottom;
        prevRowExterior = new Dimensions2(rowExterior.From, Math.max(childBranchBottom, rowExterior.To));
      }
    }
  }
  ApplyHorizontalLayout(state, level) {
    let node = level.BranchRoot;
    for (let child of node.Children) {
      LayoutAlgorithm2.HorizontalLayout(state, child);
    }
    if (node.ChildCount > 0) {
      if (this.Orientation == StackOrientation2.SingleRowHorizontal) {
        let width = node.Children[node.State.NumberOfSiblings - 1].State.Right - node.Children[0].State.Left;
        node.State.Size = new Size2(Math.max(node.State.Size.Width, width), node.State.Size.Height);
        let center = (node.Children[0].State.Left + node.Children[node.ChildCount - 1].State.Right) / 2;
        let desiredCenter = node.State.CenterH;
        let diff = desiredCenter - center;
        LayoutAlgorithm2.MoveChildrenOnly(state, level, diff);
      } else if (this.Orientation == StackOrientation2.SingleColumnVertical) {
        LayoutAlgorithm2.AlignHorizontalCenters(state, level, node.Children);
        let center = node.Children[0].State.CenterH;
        let desiredCenter = node.State.CenterH;
        let diff = desiredCenter - center;
        LayoutAlgorithm2.MoveChildrenOnly(state, level, diff);
      }
    }
  }
  RouteConnectors(state, node) {
  }
}
