import Point2 from "./Point";
import LayoutStrategyBase2 from "./LayoutStrategyBase";
import Dimensions2 from "./Dimensions";
import LayoutAlgorithm2 from "./LayoutAlgorithm";
import Box2 from "./Box";
import BranchParentAlignment2 from "./BranchParentAlignment";
import Rect2 from "./Rect";
import Edge2 from "./Edge";
import Connector2 from "./Connector";
export default class LinearLayoutStrategy extends LayoutStrategyBase2 {
  constructor() {
    super(...arguments);
    this.GetSupportsAssistants = () => true;
  }
  PreProcessThisNode(state, node) {
    if (node.ChildCount > 0) {
      node.State.NumberOfSiblings = node.Element.IsCollapsed ? 0 : node.ChildCount;
      if (!node.Element.IsCollapsed) {
        const verticalSpacer = Box2.Special(Box2.None, node.Element.Id, false);
        node.AddRegularChildBox(verticalSpacer);
        const horizontalSpacer = Box2.Special(Box2.None, node.Element.Id, false);
        node.AddRegularChildBox(horizontalSpacer);
      }
    }
  }
  ApplyVerticalLayout(state, level) {
    let node = level.BranchRoot;
    if (node.Level == 0) {
      node.State.SiblingsRowV = new Dimensions2(node.State.Top, node.State.Bottom);
    }
    if (node.AssistantsRoot != null) {
      LayoutAlgorithm2.CopyExteriorFrom(node.AssistantsRoot.State, node.State);
      LayoutAlgorithm2.VerticalLayout(state, node.AssistantsRoot);
    }
    if (node.State.NumberOfSiblings == 0) {
      return;
    }
    let siblingsRowExterior = Dimensions2.MinMax();
    let top;
    if (node.AssistantsRoot == null) {
      if (node.State.SiblingsRowV == null) {
        throw Error("SiblingsRowV is null");
      }
      top = node.State.SiblingsRowV.To + this.ParentChildSpacing;
    } else {
      top = node.State.BranchExterior.Bottom + this.ParentChildSpacing;
    }
    for (let i = 0; i < node.State.NumberOfSiblings; i++) {
      let child = node.Children[i];
      let rect = child.State;
      LayoutAlgorithm2.MoveTo(child.State, 0, top);
      if (child.State.Size == null) {
        throw Error("Size is null");
      }
      child.State.BranchExterior = Rect2.from(child.State.Size, child.State.TopLeft);
      if (rect.Size == null) {
        throw Error("Size is null");
      }
      siblingsRowExterior = Dimensions2.add(siblingsRowExterior, new Dimensions2(top, top + rect.Size.Height));
    }
    siblingsRowExterior = new Dimensions2(siblingsRowExterior.From, siblingsRowExterior.To);
    for (let i = 0; i < node.State.NumberOfSiblings; i++) {
      let child = node.Children[i];
      child.State.SiblingsRowV = siblingsRowExterior;
      LayoutAlgorithm2.VerticalLayout(state, child);
    }
  }
  ApplyHorizontalLayout(state, level) {
    let node = level.BranchRoot;
    if (node.AssistantsRoot != null) {
      LayoutAlgorithm2.HorizontalLayout(state, node.AssistantsRoot);
    }
    for (let i = 0; i < node.State.NumberOfSiblings; i++) {
      let child = node.Children[i];
      LayoutAlgorithm2.HorizontalLayout(state, child);
    }
    if (node.Level > 0 && node.ChildCount > 0) {
      let rect = node.State;
      let leftmost = node.Children[0].State.CenterH;
      let rightmost = node.Children[node.State.NumberOfSiblings - 1].State.CenterH;
      let desiredCenter = node.State.NumberOfSiblings == 1 || this.ParentAlignment == BranchParentAlignment2.Center ? leftmost + (rightmost - leftmost) / 2 : this.ParentAlignment == BranchParentAlignment2.Left ? leftmost + this.ChildConnectorHookLength : rightmost - this.ChildConnectorHookLength;
      let center = rect.CenterH;
      let diff = center - desiredCenter;
      LayoutAlgorithm2.MoveChildrenOnly(state, level, diff);
      let verticalSpacer = node.Children[node.State.NumberOfSiblings];
      if (node.Children[0].State.SiblingsRowV == null) {
        throw Error("SiblingsRowV is null");
      }
      LayoutAlgorithm2.AdjustSpacer(verticalSpacer.State, center - this.ParentConnectorShield / 2, rect.Bottom, this.ParentConnectorShield, node.Children[0].State.SiblingsRowV.From - rect.Bottom);
      state.MergeSpacer(verticalSpacer);
      let firstInRow = node.Children[0].State;
      let horizontalSpacer = node.Children[node.State.NumberOfSiblings + 1];
      if (firstInRow.SiblingsRowV == null) {
        throw Error("SiblingsRowV is null");
      }
      LayoutAlgorithm2.AdjustSpacer(horizontalSpacer.State, firstInRow.Left, firstInRow.SiblingsRowV.From - this.ParentChildSpacing, node.Children[node.State.NumberOfSiblings - 1].State.Right - firstInRow.Left, this.ParentChildSpacing);
      state.MergeSpacer(horizontalSpacer);
    }
  }
  RouteConnectors(state, node) {
    let normalChildCount = node.State.NumberOfSiblings;
    let count = normalChildCount == 0 ? 0 : normalChildCount == 1 ? 1 : 1 + 1 + normalChildCount;
    if (count == 0) {
      node.State.Connector = null;
      return;
    }
    let segments = [];
    let rootRect = node.State;
    let center = rootRect.CenterH;
    if (node.Children == null) {
      throw new Error("State is present, but children not set");
    }
    if (count == 1) {
      segments[0] = new Edge2(new Point2(center, rootRect.Bottom), new Point2(center, node.Children[0].State.Top));
    } else {
      if (node.Children[0].State.SiblingsRowV == null) {
        throw Error("SiblingsRowV is null");
      }
      let space = node.Children[0].State.SiblingsRowV.From - rootRect.Bottom;
      segments[0] = new Edge2(new Point2(center, rootRect.Bottom), new Point2(center, rootRect.Bottom + space - this.ChildConnectorHookLength));
      for (let i = 0; i < normalChildCount; i++) {
        let childRect = node.Children[i].State;
        let childCenter = childRect.CenterH;
        segments[1 + i] = new Edge2(new Point2(childCenter, childRect.Top), new Point2(childCenter, childRect.Top - this.ChildConnectorHookLength));
      }
      segments[count - 1] = new Edge2(new Point2(segments[1].To.X, segments[1].To.Y), new Point2(segments[count - 2].To.X, segments[1].To.Y));
    }
    node.State.Connector = new Connector2(segments);
  }
}
