import LayoutStrategyBase2 from "./LayoutStrategyBase";
import Box2 from "./Box";
import Dimensions2 from "./Dimensions";
import LayoutAlgorithm2 from "./LayoutAlgorithm";
import BranchParentAlignment2 from "./BranchParentAlignment";
import Edge2 from "./Edge";
import Point2 from "./Point";
import Connector2 from "./Connector";
import Rect2 from "./Rect";
export default class SingleColumnLayoutStrategy extends LayoutStrategyBase2 {
  constructor() {
    super(...arguments);
    this.GetSupportsAssistants = () => true;
  }
  PreProcessThisNode(state, node) {
    if (this.ParentAlignment != BranchParentAlignment2.Left && this.ParentAlignment != BranchParentAlignment2.Right) {
      throw new Error("InvalidOperationException: Unsupported value for ParentAlignment");
    }
    node.State.NumberOfSiblings = node.Element.IsCollapsed ? 0 : node.ChildCount;
    if (node.State.NumberOfSiblings > 0 && node.Level > 0) {
      node.State.NumberOfSiblingColumns = 1;
      node.State.NumberOfSiblingRows = node.ChildCount;
      let verticalSpacer = Box2.Special(Box2.None, node.Element.Id, false);
      node.AddRegularChildBox(verticalSpacer);
    }
  }
  ApplyVerticalLayout(state, level) {
    let node = level.BranchRoot;
    if (node.Level == 0) {
      node.State.SiblingsRowV = new Dimensions2(node.State.Top, node.State.Bottom);
    }
    if (node.AssistantsRoot != null) {
      LayoutAlgorithm2.CopyExteriorFrom(node.AssistantsRoot.State, node.State);
      LayoutAlgorithm2.VerticalLayout(state, node.AssistantsRoot);
    }
    let prevRowExterior = new Dimensions2(node.State.SiblingsRowV.From, node.AssistantsRoot == null ? node.State.SiblingsRowV.To : node.State.BranchExterior.Bottom);
    for (let row = 0; row < node.State.NumberOfSiblings; row++) {
      let child = node.Children[row];
      let rect = child.State;
      let top = prevRowExterior.To + (row == 0 ? this.ParentChildSpacing : this.SiblingSpacing);
      LayoutAlgorithm2.MoveTo(child.State, rect.Left, top);
      child.State.BranchExterior = Rect2.from(child.State.Size, child.State.TopLeft);
      let rowExterior = new Dimensions2(top, top + rect.Size.Height);
      child = node.Children[row];
      child.State.SiblingsRowV = rowExterior;
      LayoutAlgorithm2.VerticalLayout(state, child);
      let childBranchBottom = child.State.BranchExterior.Bottom;
      prevRowExterior = new Dimensions2(rowExterior.From, Math.max(childBranchBottom, rowExterior.To));
    }
  }
  ApplyHorizontalLayout(state, level) {
    let node = level.BranchRoot;
    let nodeState = node.State;
    if (node.AssistantsRoot != null) {
      LayoutAlgorithm2.HorizontalLayout(state, node.AssistantsRoot);
    }
    for (let row = 0; row < nodeState.NumberOfSiblings; row++) {
      let child = node.Children[row];
      LayoutAlgorithm2.HorizontalLayout(state, child);
    }
    let edges = LayoutAlgorithm2.AlignHorizontalCenters(state, level, this.EnumerateColumn(node));
    if (node.Level > 0 && node.ChildCount > 0) {
      let rect = node.State;
      let diff;
      if (this.ParentAlignment == BranchParentAlignment2.Left) {
        let desiredLeft = rect.CenterH + this.ParentConnectorShield / 2;
        diff = desiredLeft - edges.From;
      } else if (this.ParentAlignment == BranchParentAlignment2.Right) {
        let desiredRight = rect.CenterH - this.ParentConnectorShield / 2;
        diff = desiredRight - edges.To;
      } else {
        throw new Error("InvalidOperationException: Invalid ParentAlignment setting");
      }
      LayoutAlgorithm2.MoveChildrenOnly(state, level, diff);
      let verticalSpacer = node.Level > 0 ? node.Children[node.ChildCount - 1] : null;
      if (verticalSpacer != null) {
        let spacerTop = node.State.Bottom;
        let spacerBottom = node.Children[node.ChildCount - 2].State.Bottom;
        LayoutAlgorithm2.AdjustSpacer(verticalSpacer.State, rect.CenterH - this.ParentConnectorShield / 2, spacerTop, this.ParentConnectorShield, spacerBottom - spacerTop);
        state.MergeSpacer(verticalSpacer);
      }
    }
  }
  EnumerateColumn(branchRoot) {
    const nodes = [];
    for (let i = 0; i < branchRoot.State.NumberOfSiblings; i++) {
      nodes.push(branchRoot.Children[i]);
    }
    return nodes;
  }
  RouteConnectors(state, node) {
    if (node.ChildCount == 0) {
      return;
    }
    let count = 1 + node.State.NumberOfSiblings;
    let segments = Array(count);
    let rootRect = node.State;
    let center = rootRect.CenterH;
    let verticalCarrierHeight = node.Children[node.State.NumberOfSiblings - 1].State.CenterV - node.State.Bottom;
    segments[0] = new Edge2(new Point2(center, rootRect.Bottom), new Point2(center, rootRect.Bottom + verticalCarrierHeight));
    for (let ix = 0; ix < node.State.NumberOfSiblings; ix++) {
      let rect = node.Children[ix].State;
      let destination = this.ParentAlignment == BranchParentAlignment2.Left ? rect.Left : rect.Right;
      segments[1 + ix] = new Edge2(new Point2(center, rect.CenterV), new Point2(destination, rect.CenterV));
    }
    node.State.Connector = new Connector2(segments);
  }
}
